<template>
<!--查询阀门当前指标,第2种展示形式-->
<div >
  <el-row :gutter="24">
    <el-col  :md="24"  >
      <div style="float:right;margin-right: 20px;">
        <el-button type="primary" icon="el-icon-search" size="small" @click="drawerVisible1=true">查询数据</el-button>

        <el-dropdown
          :hide-on-click="false"
          _style="float:right;margin-right: 20px;"
        >
          <el-button type="primary" size="small" title="显示隐藏列" > <i class="el-icon-s-grid ">动态列</i></el-button>

          <el-dropdown-menu slot="dropdown">
            <el-scrollbar  style="height: 40vh; width:150px;">
              <el-checkbox-group v-model="colsChecked">
                <el-dropdown-item v-for="item in colsMap" :key="item.value">
                  <el-checkbox @change="colCheckCommand" :label="item.value" :key="item.value">{{item.caption}}</el-checkbox>
                </el-dropdown-item>
              </el-checkbox-group>
            </el-scrollbar>

          </el-dropdown-menu>
        </el-dropdown>

      </div>

    </el-col>
  </el-row>
  <!--talbe start-->
  <el-card  style="display:flex;justify-content:center;">
  <el-table
    border

    ref="multipleTable"
    :data="tableData"
    tooltip-effect="dark"
    @sort-change="sortColumn"

    :row-style="{'height':'10px'}"
    :cell-style="{'padding':'5px 0px'}"
    @selection-change="handleSelectionChange"

    :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' }"
  >
    <el-table-column
      fixed
      v-if="colsMap[0].isShow"
      prop="vi_setup_position"
      label="安装地址"
      align="center"
      show-overflow-tooltip
      width="120"
    >

    </el-table-column>
    <el-table-column
      fixed
      v-if="colsMap[1].isShow"
      prop="vi_describe"
      label="阀门描述"
      align="center"
      show-overflow-tooltip
      width="120"
    >
    </el-table-column>
    <el-table-column
      fixed
      v-if="colsMap[2].isShow"
      prop="vci_back_water_value"
      label="回水温度"
      align="center"
      show-overflow-tooltip
      width="80"
    >
      <template slot-scope="scope">

        <el-tag type="success" effect="dark" size="mini" v-if="scope.row.vci_back_water_value===undefined">未知</el-tag>
        <el-tag type="success"  effect="dark" size="mini" v-else>{{ scope.row.vci_back_water_value }}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      fixed
      v-if="colsMap[3].isShow"
      prop="vci_entry_water_value"
      label="进水温度"
      align="center"
      show-overflow-tooltip
      width="80"
    >
      <template slot-scope="scope">

        <el-tag type="warning" effect="dark" size="mini" v-if="scope.row.vci_entry_water_value===undefined">未知</el-tag>
        <el-tag type="warning"  effect="dark" size="mini" v-else>{{ scope.row.vci_entry_water_value }}</el-tag>
      </template>
    </el-table-column>

    <el-table-column
      fixed
      v-if="colsMap[4].isShow"
      prop="vci_open_value"
      label="实际开度"
      align="center"
      show-overflow-tooltip
      width="80"
    >
      <template slot-scope="scope">

        <el-tag type="primary" effect="dark" size="mini" v-if="scope.row.vci_open_value===undefined">未知</el-tag>
        <el-tag type="primary"  effect="dark" size="mini" v-else>{{ scope.row.vci_open_value }}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      fixed
      v-if="colsMap[5].isShow"
      prop="vci_setting_open_value"
      label="设定开度"
      align="center"
      show-overflow-tooltip
      width="80"
    >
      <template slot-scope="scope">

        <el-tag type="default" effect="dark" size="mini" v-if="scope.row.vci_setting_open_value===undefined">未知</el-tag>
        <el-tag type="default"  effect="dark" size="mini" v-else>{{ scope.row.vci_setting_open_value }}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      fixed
      v-if="colsMap[6].isShow"
      prop="vci_g_address"
      label="网关地址"
      align="center"
      show-overflow-tooltip
      width="100"
    >

    </el-table-column>
    <el-table-column
      fixed
      v-if="colsMap[7].isShow"
      prop="vci_c_address"
      label="集中器地址"
      align="center"
      show-overflow-tooltip
      width="100"
    >

    </el-table-column>
    <el-table-column
      fixed
      v-if="colsMap[8].isShow"
      prop="vci_v_address"
      label="阀门地址"
      align="center"
      show-overflow-tooltip
      width="100"
    >

    </el-table-column>
    <el-table-column
      fixed
      v-if="colsMap[9].isShow"
      prop="vci_sendcmd_writetime"
      _label="下达指令时间"
      align="center"
      show-overflow-tooltip
      width="100"
    >
      <template
        slot="header"
        slot-scope="scope"
      >


        <el-tooltip
          class="item"
          effect="dark"
          content="下达采集回水温度指令时间"
          placement="top"
        >
          <span style='display:block;overflow:hidden; text-overflow:ellipsis;white-space:nowrap;word-break:keep-all;' >下达采集回水温度指令时间</span>
        </el-tooltip>
      </template>

      <template slot-scope="scope">

        <el-tag type="danger" effect="dark" size="mini" style='display:inline-block;word-wrap:break-word;white-space:normal;' v-if="scope.row.vci_sendcmd_writetime>scope.row.vci_writetime">{{ scope.row.vci_sendcmd_writetime }}</el-tag>
        <span  size="mini" style='display:inline-block;word-wrap:break-word;white-space:normal;' v-else>{{ scope.row.vci_sendcmd_writetime }}</span>
      </template>
    </el-table-column>
    <el-table-column
      fixed
      v-if="colsMap[10].isShow"
      prop="vci_writetime"
      label="回水温度入库时间"
      align="center"
      show-overflow-tooltip
      width="100"
    >
      <template
        slot="header"
        slot-scope="scope"
      >


        <el-tooltip
          class="item"
          effect="dark"
          content="回水温度入库时间"
          placement="top"
        >
          <span style='display:block;overflow:hidden; text-overflow:ellipsis;white-space:nowrap;word-break:keep-all;' >回水温度入库时间</span>
        </el-tooltip>
      </template>

    </el-table-column>
  </el-table>
  </el-card>
  <!--talbe end-->
  <!--page start-->
  <el-pagination
    background
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
    :current-page="queryParam.currentPage"
    :page-sizes="[10, 20, 50, 100,200]"
    :page-size="queryParam.pageSize"
    layout="total, sizes, prev, pager, next, jumper"
    :total="queryParam.totalRow">
  </el-pagination>
  <!--page end-->

  <!--drawer start-->
  <el-drawer

    :visible.sync="drawerVisible1"
    direction="ttb"
    :wrapperClosable="false"
    :close-on-press-escape="false"
    ref="drawer"
    style=" margin-left:20%;width:60%;margin-right: 20%;"

  >
    <span slot="title" style="text-align: left;">查询条件</span>
    <!--form start-->
    <el-form label-position="left" :model="ruleForm" :rules="rules" ref="ruleForm2" label-width="130px"  style="text-align: left;font-size:20px;font-weight: bold;">
      <el-card shadow="always" :body-style="cardBodyStyle" style="">

        <div v-show="cardBodyHeight">
          <el-row :gutter="24"  >

            <el-col :md="12" >
              <el-form-item  label="阀门安装地址" prop="vi_setup_position"  >
                <el-input v-model="ruleForm.vi_setup_position"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="12" >
              <el-form-item label="回水温度区间" >
                <el-col :span="5" style="width:45%;padding:0px;">
                  <el-form-item    prop="valve_currentdata_backwater_1"   :rules="this.rules.valve_currentdata_backwater_1"  >
                    <el-input v-model="ruleForm.valve_currentdata_backwater_1" placeholder="数字或浮点数"  ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="2" style="width:10%;padding:0px;">
                  <i class="el-icon-minus" style="text-align:center;width:100%;padding:0px;"></i>
                </el-col>
                <el-col :md="5" style="width:45%;padding:0px;">
                  <el-form-item    prop="valve_currentdata_backwater_2" :rules="this.rules.valve_currentdata_backwater_2"  >
                    <el-input v-model="ruleForm.valve_currentdata_backwater_2" placeholder="数字或浮点数"  ></el-input>
                  </el-form-item>
                </el-col>
              </el-form-item>
            </el-col>

          </el-row>
          <el-row :gutter="24"  >
            <el-col :md="12" >
              <el-form-item label="阀门开度区间" >
                <el-col :span="5" style="width:45%;padding:0px;">
                  <el-form-item    prop="valve_openvalue_1"   :rules="this.rules.valve_openvalue_1"  >
                    <el-input v-model="ruleForm.valve_openvalue_1" placeholder="数字或浮点数"  ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="2" style="width:10%;padding:0px;">
                  <i class="el-icon-minus" style="text-align:center;width:100%;padding:0px;"></i>
                </el-col>
                <el-col :md="5" style="width:45%;padding:0px;">
                  <el-form-item    prop="valve_openvalue_2"  :rules="this.rules.valve_openvalue_2"   >
                    <el-input v-model="ruleForm.valve_openvalue_2" placeholder="数字或浮点数"  ></el-input>
                  </el-form-item>
                </el-col>
              </el-form-item>
            </el-col>
            <el-col :md="12" >
              <el-button style="float:right;" type="primary" @click="queryValveCurrentData(1)">查询</el-button>
            </el-col>
          </el-row>

          <el-row  :gutter="24" _type="flex" _justify="space-around">


            <!--          <el-col :md="12">-->
            <!--            <el-form-item label="商户身高" prop="height1" required :rules="this.rules2.height1">-->
            <!--              <el-input v-model="ruleForm2.height1"></el-input>-->
            <!--            </el-form-item>-->
            <!--          </el-col>-->
            <!--          <el-col :md="12">-->
            <!--            <el-form-item label="商户现在住址" prop="address1" required :rules="this.rules2.address1">-->
            <!--              <el-input v-model="ruleForm2.address1"></el-input>-->
            <!--            </el-form-item>-->
            <!--          </el-col>-->

          </el-row>
        </div>

      </el-card>
    </el-form>
    <!--form end-->


  </el-drawer>
  <!--drawer end-->
</div>


</template>

<script>
export default {
  name: "QueryValveCurrentDataForSa",
  data(){
    let validateSetupPosition = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('阀门安装地址不可为空'));
      } else {
        callback();
      }
    };
    let validateBackwater = (rule, value, callback) => {
      if (value.trim().length !== 0 && this.customGlobal.isnumberandfloat(value)==false) {
        callback(new Error('回水温度区间必须是数字或浮点数'));
      } else {
        callback();
      }
    };
    let validateOpenvalue = (rule, value, callback) => {
      if (value.trim().length !== 0 && this.customGlobal.isnumberandfloat(value)==false) {
        callback(new Error('阀门开度区间必须是数字或浮点数'));
      } else {
        callback();
      }
    };
    return {

      drawerVisible1:false,
      // clientHeight :(window.innerHeight-105),
      // clientHeight :(document.documentElement.clientHeight-105),
      cardBodyHeight:true,
      cardBodyStyle:'padding-top:10px;padding-bottom:10px;',
      icon1:'el-icon-caret-top',

      tableData:[],//表格数据
      multipleSelection: [],
      sortOrderCol:'',
      colsChecked:[
        'vi_setup_position','vi_describe','vci_back_water_value','vci_entry_water_value','vci_open_value','vci_setting_open_value','vci_g_address','vci_c_address','vci_v_address','vci_sendcmd_writetime','vci_writetime'
      ],
      colsMap:[
        {caption:'安装地址',value:'vi_setup_position',isShow:true},
        {caption:'阀门描述',value:'vi_describe',isShow:true},
        {caption:'回水温度',value:'vci_back_water_value',isShow:true},
        {caption:'进水温度',value:'vci_entry_water_value',isShow:true},
        {caption:'实际开度',value:'vci_open_value',isShow:true},
        {caption:'设定开度',value:'vci_setting_open_value',isShow:true},
        {caption:'网关地址',value:'vci_g_address',isShow:true},
        {caption:'集中器地址',value:'vci_c_address',isShow:true},
        {caption:'阀门地址',value:'vci_v_address',isShow:true},
        {caption:'下达采集回水温度指令时间',value:'vci_sendcmd_writetime',isShow:true},
        {caption:'回水温度入库时间',value:'vci_writetime',isShow:true},
      ],

      queryParam:{
        totalRow:0,
        currentPage:1,
        pageSize:this.customGlobal.pageSize,
      },


      ruleForm: {
        vi_setup_position: '',
        valve_currentdata_backwater_1: '',
        valve_currentdata_backwater_2: '',
        valve_openvalue_1: '',
        valve_openvalue_2: '',


      },
      rules: {
        vi_setup_position: [
            // {validator: validateSetupPosition, trigger:'blur'}
        ],
        valve_currentdata_backwater_1:[
          {validator: validateBackwater, trigger:'change'}
        ],
        valve_currentdata_backwater_2:[
          {validator: validateBackwater, trigger:'change'}
        ],

        valve_openvalue_1:[
          {validator: validateOpenvalue, trigger:'change'}
        ],
        valve_openvalue_2:[
          {validator: validateOpenvalue, trigger:'change'}
        ],
      },


    }

  },
  mounted() {
    // let that = this;
    // window.onresize= function temp() {
    //   that.clientHeight = (window.innerHeight-105);
    // };
  },
  methods:{
    confCardBodyHeigth(){

      if(this.cardBodyHeight===false){
        this.cardBodyHeight = true;
        this.cardBodyStyle = 'padding-top:10px;padding-bottom:10px;';
        this.icon1='el-icon-caret-top';
      }else{
        this.cardBodyHeight = false;
        this.cardBodyStyle = 'padding-top:0px;padding-bottom:0px;';
        this.icon1='el-icon-caret-bottom';
      }
      console.info("confCardBodyHeigth:"+this.cardBodyHeight);
    },
    queryValveCurrentData(_cp_){
      let that = this;
      //console.info("form元素："+JSON.stringify( this.ruleForm ));
      let _jsondata_ =   {

          perPageSize:this.queryParam.pageSize,
          currentPage:(_cp_===1 ? 1: this.queryParam.currentPage),
          vi_setup_position:this.ruleForm.vi_setup_position,
          valve_currentdata_backwater_1:this.ruleForm.valve_currentdata_backwater_1,
          valve_currentdata_backwater_2:this.ruleForm.valve_currentdata_backwater_2,
          valve_openvalue_1:this.ruleForm.valve_openvalue_1,
          valve_openvalue_2:this.ruleForm.valve_openvalue_2,


      } ;
      //console.info("json:"+JSON.stringify(_jsondata_));

      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });

      var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.postQueryValveCurrentDataList,
                this.customGlobal.serverUrl.baseURI,
                 {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
        );

      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;

        }else{
          console.info("查询失败");
          loading.close();

          that.tableData = [];
          this.$alert(res.data.msg, '查询失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
                if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                  this.$router.replace({path:'/login'});
                }
            }
          });
        }
        this.drawerVisible1=false;
      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();

        this.drawerVisible1=false;

        that.tableData = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        this.drawerVisible1=false;

        that.tableData = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },
    colCheckCommand(){
      console.info("colsChecked："+this.colsChecked);
      this.colsMap.filter(i => {
        console.info("遍历"+i.value);
        if (this.colsChecked.indexOf(i.value) !== -1) {
          console.info("选中集合中存在"+i.value);
          i.isShow = true
        } else {
          i.isShow = false
        }
      })

      console.info('colsList:'+JSON.stringify(this.colsMap));
    },
    sortColumn(column) {
      console.log("已经排序项：" + JSON.stringify(column));
      let orderFlag = '';
      if (column.order != null) {
        if (column.order === "descending") {
          orderFlag = 'desc';
        } else if (column.order === "ascending") {
          orderFlag = 'asc';
        }
        console.info("属性名称："+ column.prop+",排序方式："+column.order);

        this.sortOrderCol = column.prop + " " + orderFlag;
        console.info("用户选择排序列："+this.sortOrderCol);
      }

    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.queryValveCurrentData(1);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val ;
      this.queryValveCurrentData(val);
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.info("选择的行："+JSON.stringify( this.multipleSelection) );
    },

  }
}
</script>

<!--<style scoped>-->
<style >
.el-drawer__header{
  margin:0px!important;
  padding:10px!important;
}
.el-card__header{
  /*background:#f5f5f5 !important;*/
  background:#f2f3f8 !important;
  /*height: 10px!important;*/
  padding:10px!important;
}
.el-card__body{
  padding-top:5px!important;
  padding-bottom:5px!important;
  padding-left:20px!important;
  padding-right:20px!important;
}
.el-form-item {margin-bottom: 3px!important;}
</style>



